export const checkStatus = (value) => {
  let message;
  let color;
  switch (value) {
    case -1:
      message = 'Khng c ph duyt';
      color = 'status-rejected';
      break;
    case 1:
      message = 'Bn nhp';
      color = 'status-new';
      break;
    case 2:
      message = 'Ch duyt';
      color = 'status-pending';
      break;
    case 3:
      message = ' duyt';
      color = 'status-approved';
      break;
    case 4:
      message = 'ang thc hin';
      color = 'status-processing';
      break;
    case 5:
      message = ' kt thc';
      color = 'status-finsished';
      break;
    case 6:
      message = 'T chi';
      color = 'status-rejected';
      break;
    case 7:
      message = 'Tip nhn';
      break;
    case 8:
      message = 't';
      color = 'status-approved';
      break;
    case 9:
      message = 'Khng t';
      color = 'status-not-pass';
      break;
    case 10:
      message = 'Yu cu chnh sa';
      color = 'status-pending';
      break;
    case 11:
      message = 'Ch i';
      break;
    case 12:
      message = 'Th vic';
      color = 'status-processing';
      break;
    case 13:
      message = 'Kt thc th vic';
      break;
    case 14:
      message = 'Chnh thc';
      color = 'status-approved';
      break;
    case 15:
      message = 'Ngh vic';
      color = 'status-not-pass';
      break;
    case 16:
      message = 'Sa thi';
      break;
    case 17:
      message = 'Ch x l';
      color = 'status-new';
      break;
    case 18:
      message = 'Ch phng vn';
      color = 'status-pending';
      break;
    case 19:
      message = 'Chuyn i h s ng vin';
      color = 'status-transfer';
      break;
    default:
      message = 'Khng trng thi';
  }
  return {
    message,
    color,
  };
};

